.top-spacer {
  height: 65vh;
}

.bottom-spacer {
  height: 110vh;
}

.sticky {
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}

.header {
  display: flex;
  justify-content: center;
  opacity: 100%;
  background-color: white;
}

.header_link {
  padding: 20px;
  color: darkgoldenrod;
  background: white;
	opacity: 100%;
  font-weight: bold;
  border: none;
  border-bottom: 3px solid transparent;
  cursor: pointer;
  outline: none;
}

.selected {
  border-bottom: 3px solid darkgoldenrod;
  color: darkgoldenrod;
}

.section {
  height: 60vh;
}

#Leadership {
  background-color: #a388e8;
}

#Providers {
  background-color: #f4769e;
}

#Operations {
  background-color: #8face0;
}
